import React from 'react';
import IconsListItem from './iconsListItem';



const IconsList = (props) => {
    const iconsItems = props.icons.map((icon,num) => {
     
       
        return (<IconsListItem
            key={num+1}
            icon={icon}
            num = {num+1}
            dark={props.dark}
            style = {props.style}
            />
        );
    });

    return (

        <div className="icon-list animated delay50 fadeIn">
         <div className="container">
            {iconsItems}
            </div>
        </div>
    );

};


export default IconsList;