import { FETCH_ICONS,SET_CATEGORY,CHANGE_CATEGORIES,SET_STYLE,FETCH_CATEGORIES,FETCH_STYLE, SET_SELECTED_ICON, CHANGE_SIDE_ICON, TOGGLE_SIDE_ICON, SET_SEARCH_TERM, FETCH_ICONS_SUCCESS, FETCH_ICONS_FAILURE, FETCH_ICON, FETCH_ICON_SUCCESS, FETCH_ICON_FAILURE } from './types';


 export function fetchIcons(term,category){
   return (dispatch) => {
    dispatch({type: "SET_STYLE", payload: 'ALL'});
   dispatch({type: "FETCH_ICONS",term:term,category:category});
 
   
}

}
 export function fetchIcon(term,style){
    return {
        type: FETCH_ICON,
        term: term,
        style: style

    };

}
export function fetchCategories() {
    return {
        type: FETCH_CATEGORIES,
        payload: ''
    };
}

 export function fetchStyle(style,term,category){
   return (dispatch) => {
    dispatch({type: "SET_STYLE", payload: style});
   dispatch({type: "FETCH_STYLE", payload: style,term:term,category:category});
  
}

}
export function setCategory(data,name,style,search) {
    return {
        type: SET_CATEGORY,
        payload: data,
        name:name,
        search:search,
        style:style
    };
}
export function setSearchTerm(data,style,category) {
    return {
        type: SET_SEARCH_TERM,
        payload: data,
        category:category,
        style:style
    };
}



export function changeSideIcon(bool) {
    return {
        type: CHANGE_SIDE_ICON,
        payload: bool
    };
}
export function changeCategories(bool) {
    return {
        type: CHANGE_CATEGORIES,
        payload: bool
    };
}
export function setSelectedIcon(name, slug, type_of_icon) {
    return {
        type: SET_SELECTED_ICON,
        name: name,
        slug: slug,
        type_of_icon: type_of_icon,
    };
}
export function toggleSideIcon() {
    return {
        type: TOGGLE_SIDE_ICON,
        payload: ''
    };
}







